@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');


  .logo {
    height: 6rem;
    width:auto;
    padding-left: 10px;
  }
  
  .navbar-nav {
    font-family: 'Quicksand', sans-serif;
        font-size: 20px;
        color:black !important;
        margin-top: 15px;
  
  }
  
  a {
    list-style-type: none;
    color:black !important;
    text-decoration: none;
    margin-top: 15px;
  }
  
  .resume-button {
    font-family: 'Quicksand', sans-serif;
    font-size: 30px;
    background-color: rgb(43, 202, 226);
    color:black;
    border-radius: 10px;
    margin: 40px 0 0 0px;
    padding: 2px 4px 2px 4px;
    height:50px;
    box-shadow: 5px 5px 5px rgb(156, 156, 156);
  }
  
  Button:hover {
    background-color: white;
    border: solid white;
    color:black;
  }
  

@media (min-width:992px) {
.logo {
  height: 5rem;
  width:auto;
}

.navbar-nav {
  font-family: 'Quicksand', sans-serif;
      font-size: 16px;
      color:black !important;

}

a {
  list-style-type: none;
  color:black !important;
  text-decoration: none !important;
  font-size: 25px !important;
}

.resume-button {
  font-family: 'Quicksand', sans-serif;
  font-size: 30px;
  background-color: rgb(43, 202, 226);
  color:black;
  border-radius: 10px;
  margin: 40px 0 0 0px;
  padding: .5rem;
}

.resume-button:hover {
  background-color: white ;
  
}
